

import React, { useEffect, useMemo, useState } from "react";
import { motion } from "framer-motion";
import {
  TrendingUp,
  TrendingDown,
  Users,
  MessageSquare,
  Clock,
  CheckCircle2,
  AlertCircle,
  ArrowUpRight,
  Calendar,
  Zap,
  Target,
  Award,
} from "lucide-react";

// Recharts for charts (install: yarn add recharts)
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
  PieChart,
  Pie,
  Cell,
  BarChart,
  Bar,
  Legend,
} from "recharts";

// -- Small UI components --
const StatCard = ({
  title,
  value,
  change,
  trend,
  icon: Icon,
  gradient,
  delay,
}: any) => {
  const [isHovered, setIsHovered] = useState(false);

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.4, delay }}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
      className="relative group"
    >
      <div className="p-6 bg-white rounded-2xl border border-slate-200 hover:border-slate-300 transition-all duration-300 hover:shadow-xl hover:shadow-slate-200/50">
        <div className="flex items-start justify-between mb-4">
          <div className="flex-1">
            <p className="text-sm font-medium text-slate-500 mb-1">{title}</p>
            <h3 className="text-3xl font-bold text-slate-800">{value}</h3>
          </div>
          <motion.div
            className={`w-12 h-12 rounded-xl bg-gradient-to-br ${gradient} flex items-center justify-center shadow-lg`}
            animate={isHovered ? { scale: 1.08, rotate: 6 } : { scale: 1, rotate: 0 }}
            transition={{ duration: 0.28 }}
          >
            <Icon className="w-6 h-6 text-white" />
          </motion.div>
        </div>

        <div className="flex items-center gap-2">
          <div
            className={`flex items-center gap-1 px-2 py-1 rounded-lg text-xs font-semibold ${
              trend === "up" ? "bg-emerald-50 text-emerald-700" : "bg-rose-50 text-rose-700"
            }`}
          >
            {trend === "up" ? <TrendingUp className="w-3 h-3" /> : <TrendingDown className="w-3 h-3" />}
            {change}
          </div>
          <span className="text-xs text-slate-500">vs last month</span>
        </div>

        <motion.div
          className="absolute inset-0 rounded-2xl bg-gradient-to-br from-white/0 to-slate-50/50 opacity-0 group-hover:opacity-100 transition-opacity duration-300 pointer-events-none"
        />
      </div>
    </motion.div>
  );
};

const ActivityItem = ({ icon: Icon, title, description, time, gradient, delay }: any) => {
  return (
    <motion.div
      initial={{ opacity: 0, x: -20 }}
      animate={{ opacity: 1, x: 0 }}
      transition={{ duration: 0.3, delay }}
      className="flex items-start gap-4 p-4 rounded-xl hover:bg-slate-50 transition-colors duration-200 group cursor-pointer"
    >
      <div
        className={`w-10 h-10 rounded-lg bg-gradient-to-br ${gradient} flex items-center justify-center flex-shrink-0 shadow-md group-hover:shadow-lg transition-shadow`}
      >
        <Icon className="w-5 h-5 text-white" />
      </div>
      <div className="flex-1 min-w-0">
        <h4 className="text-sm font-semibold text-slate-800 mb-1">{title}</h4>
        <p className="text-xs text-slate-500 truncate">{description}</p>
      </div>
      <div className="flex items-center gap-1 text-xs text-slate-400">
        <Clock className="w-3 h-3" />
        {time}
      </div>
    </motion.div>
  );
};

// -- Analytics helper utilities --
const DAYS = 14;
const makePastDates = (n = DAYS) => {
  const arr = [];
  for (let i = n - 1; i >= 0; i--) {
    const d = new Date();
    d.setDate(d.getDate() - i);
    arr.push(d.toISOString().slice(0, 10));
  }
  return arr;
};

// random helper
const rand = (min: number, max: number) => Math.floor(Math.random() * (max - min + 1)) + min;

export default function OverviewWithAnalytics() {
  const [activeTab, setActiveTab] = useState<"overview" | "analytics">("overview");

  // --- Stats (keeps original layout values, but dynamic) ---
  const [stats] = useState(() => [
    {
      title: "Total Messages",
      value: "2,845",
      change: "+12.5%",
      trend: "up",
      icon: MessageSquare,
      gradient: "from-blue-500 to-cyan-500",
    },
    {
      title: "Active Users",
      value: "1,234",
      change: "+8.2%",
      trend: "up",
      icon: Users,
      gradient: "from-emerald-500 to-teal-500",
    },
    {
      title: "Response Time",
      value: "2.4h",
      change: "-15.3%",
      trend: "up",
      icon: Clock,
      gradient: "from-violet-500 to-purple-500",
    },
    {
      title: "Completion Rate",
      value: "94.2%",
      change: "+3.1%",
      trend: "up",
      icon: CheckCircle2,
      gradient: "from-orange-500 to-rose-500",
    },
  ]);

  const activities = [
    {
      icon: MessageSquare,
      title: "New message from Sarah Johnson",
      description: "Regarding project timeline and deliverables",
      time: "2m ago",
      gradient: "from-blue-500 to-cyan-500",
    },
    {
      icon: Users,
      title: "Team meeting scheduled",
      description: "Weekly sync-up with development team",
      time: "15m ago",
      gradient: "from-emerald-500 to-teal-500",
    },
    {
      icon: CheckCircle2,
      title: "Task completed",
      description: "UI/UX design review has been finalized",
      time: "1h ago",
      gradient: "from-violet-500 to-purple-500",
    },
    {
      icon: AlertCircle,
      title: "Attention required",
      description: "3 messages awaiting your response",
      time: "2h ago",
      gradient: "from-orange-500 to-rose-500",
    },
    {
      icon: Target,
      title: "Goal achieved",
      description: "Monthly target reached ahead of schedule",
      time: "3h ago",
      gradient: "from-pink-500 to-rose-500",
    },
  ];

  // --- Analytics state (simulated live data) ---
  const labels = useMemo(() => makePastDates(14), []);

  const [messagesPerDay, setMessagesPerDay] = useState(
    labels.map((label) => ({ date: label, messages: rand(120, 420) }))
  );

  // role breakdown (admin, employee, manager)
  const [roleBreakdown, setRoleBreakdown] = useState([
    { name: "Admin", value: 1200 },
    { name: "Employee", value: 820 },
    { name: "Manager", value: 825 },
  ]);

  // team breakdown
  const [teamBreakdown, setTeamBreakdown] = useState([
    { team: "Sales", messages: 820 },
    { team: "Support", messages: 1120 },
    { team: "Engineering", messages: 630 },
    { team: "Marketing", messages: 410 },
  ]);

  // top responders / performers
  const [topResponders, setTopResponders] = useState([
    { name: "Aisha Khan", messages: 420, role: "Support" },
    { name: "Rahul Mehta", messages: 388, role: "Sales" },
    { name: "Priya Sharma", messages: 335, role: "Support" },
  ]);

  const [topPerformers, setTopPerformers] = useState([
    { name: "Team Alpha", metric: "94.2%" },
    { name: "Team Beta", metric: "91.8%" },
    { name: "Team Gamma", metric: "89.6%" },
  ]);

  // Simulate live updates: every 3s add random messages to today's bucket and randomly shift role/team counts.
  useEffect(() => {
    const t = setInterval(() => {
      setMessagesPerDay((prev) => {
        const copy = [...prev];
        const today = copy[copy.length - 1];
        const delta = rand(1, 15);
        today.messages += delta;
        // rotate older data slightly to simulate small natural changes
        for (let i = 0; i < copy.length - 1; i++) {
          copy[i].messages = Math.max(10, copy[i].messages + rand(-2, 3));
        }
        return copy.slice();
      });

      setRoleBreakdown((prev) => {
        const copy = prev.map((r) => ({ ...r, value: Math.max(10, r.value + rand(-6, 8)) }));
        return copy;
      });

      setTeamBreakdown((prev) => prev.map((t) => ({ ...t, messages: Math.max(5, t.messages + rand(-8, 12)) })));

      setTopResponders((prev) => {
        // slightly shuffle counts
        const copy = prev.map((p) => ({ ...p, messages: p.messages + rand(-3, 6) }));
        copy.sort((a, b) => b.messages - a.messages);
        return copy;
      });
    }, 3000);

    return () => clearInterval(t);
  }, []);

  // chart colors
  const pieColors = ["#0ea5e9", "#34d399", "#fb7185"];
  const barColors = ["#60a5fa", "#34d399", "#f472b6", "#f59e0b"];
  const lineColor = "#06b6d4";

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 via-white to-slate-50 p-4 sm:p-6 lg:p-8">
      <div className="max-w-7xl mx-auto">
        {/* header + tabs */}
        <motion.div initial={{ opacity: 0, y: -20 }} animate={{ opacity: 1, y: 0 }} transition={{ duration: 0.5 }} className="mb-6">
          <div className="flex items-center justify-between mb-4">
            <div className="flex items-center gap-3">
              <h1 className="text-3xl md:text-4xl font-bold bg-gradient-to-r from-slate-800 to-slate-600 bg-clip-text text-transparent">Overview</h1>
              <motion.div animate={{ scale: [1, 1.2, 1] }} transition={{ duration: 2, repeat: Infinity }}>
                <Zap className="w-6 h-6 text-yellow-500" />
              </motion.div>
              <p className="text-sm text-slate-500 ml-3 flex items-center gap-2">
                <Calendar className="w-4 h-4" /> Welcome back! Here's what's happening today.
              </p>
            </div>

            <div className="flex items-center gap-2">
              <div className="rounded-full bg-white/40 p-1 border border-slate-100">
                <button
                  className={`px-3 py-1 rounded-full text-sm font-medium ${activeTab === "overview" ? "bg-slate-900 text-white" : "text-slate-600"}`}
                  onClick={() => setActiveTab("overview")}
                >
                  Overview
                </button>
                <button
                  className={`ml-2 px-3 py-1 rounded-full text-sm font-medium ${activeTab === "analytics" ? "bg-slate-900 text-white" : "text-slate-600"}`}
                  onClick={() => setActiveTab("analytics")}
                >
                  Analytics
                </button>
              </div>
              <button className="text-sm font-medium text-blue-600 hover:text-blue-700 flex items-center gap-1 transition-colors">
                Export
                <ArrowUpRight className="w-4 h-4" />
              </button>
            </div>
          </div>
        </motion.div>

        {/* tab panels */}
        {activeTab === "overview" ? (
          <div>
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-4 sm:gap-6 mb-8">
              {stats.map((stat, index) => (
                <StatCard key={stat.title} {...stat} delay={index * 0.08} />
              ))}
            </div>

            <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
              <motion.div initial={{ opacity: 0, y: 20 }} animate={{ opacity: 1, y: 0 }} transition={{ duration: 0.4, delay: 0.4 }} className="lg:col-span-2">
                <div className="bg-white rounded-2xl border border-slate-200 p-6 shadow-sm">
                  <div className="flex items-center justify-between mb-6">
                    <h2 className="text-xl font-bold text-slate-800">Recent Activity</h2>
                    <button className="text-sm font-medium text-blue-600 hover:text-blue-700 flex items-center gap-1 transition-colors">
                      View all
                      <ArrowUpRight className="w-4 h-4" />
                    </button>
                  </div>
                  <div className="space-y-2">
                    {activities.map((activity, index) => (
                      <ActivityItem key={index} {...activity} delay={0.5 + index * 0.08} />
                    ))}
                  </div>
                </div>
              </motion.div>

              <motion.div initial={{ opacity: 0, y: 20 }} animate={{ opacity: 1, y: 0 }} transition={{ duration: 0.4, delay: 0.5 }} className="space-y-6">
                <div className="bg-gradient-to-br from-blue-500 to-cyan-500 rounded-2xl p-6 text-white relative overflow-hidden shadow-xl">
                  <div className="relative z-10">
                    <div className="w-12 h-12 bg-white/20 rounded-xl flex items-center justify-center mb-4 backdrop-blur-sm">
                      <Award className="w-6 h-6" />
                    </div>
                    <h3 className="text-lg font-bold mb-2">Performance Goal</h3>
                    <p className="text-sm text-blue-50 mb-4">You're 94% towards your monthly goal. Keep it up!</p>
                    <div className="w-full bg-white/20 rounded-full h-2 mb-2 backdrop-blur-sm">
                      <motion.div initial={{ width: 0 }} animate={{ width: "94%" }} transition={{ duration: 1, delay: 0.8 }} className="h-full bg-white rounded-full" />
                    </div>
                    <p className="text-xs text-blue-100">2,680 / 2,850 messages</p>
                  </div>
                  <div className="absolute -right-8 -bottom-8 w-32 h-32 bg-white/10 rounded-full blur-3xl" />
                </div>

                <div className="bg-white rounded-2xl border border-slate-200 p-6 shadow-sm">
                  <h3 className="text-lg font-bold text-slate-800 mb-4">Quick Stats</h3>
                  <div className="space-y-4">
                    <div className="flex items-center justify-between">
                      <span className="text-sm text-slate-600">Avg. Response</span>
                      <span className="text-sm font-semibold text-slate-800">2.4 hours</span>
                    </div>
                    <div className="flex items-center justify-between">
                      <span className="text-sm text-slate-600">Success Rate</span>
                      <span className="text-sm font-semibold text-emerald-600">94.2%</span>
                    </div>
                    <div className="flex items-center justify-between">
                      <span className="text-sm text-slate-600">Active Teams</span>
                      <span className="text-sm font-semibold text-slate-800">8</span>
                    </div>
                    <div className="flex items-center justify-between">
                      <span className="text-sm text-slate-600">Pending Tasks</span>
                      <span className="text-sm font-semibold text-orange-600">12</span>
                    </div>
                  </div>
                </div>
              </motion.div>
            </div>
          </div>
        ) : (
          // --- ANALYTICS TAB ---
          <div>
            <div className="grid grid-cols-1 lg:grid-cols-3 gap-6 mb-6">
              <div className="lg:col-span-2 bg-white rounded-2xl border border-slate-200 p-4 shadow-sm">
                <h3 className="text-lg font-bold text-slate-800 mb-3">Messages (Last 14 days)</h3>
                <div style={{ height: 300 }}>
                  <ResponsiveContainer width="100%" height="100%">
                    <LineChart data={messagesPerDay} margin={{ top: 5, right: 20, left: 0, bottom: 5 }}>
                      <CartesianGrid strokeDasharray="3 3" />
                      <XAxis dataKey="date" tick={{ fontSize: 11 }} />
                      <YAxis />
                      <Tooltip />
                      <Line type="monotone" dataKey="messages" stroke={lineColor} strokeWidth={3} dot={false} />
                    </LineChart>
                  </ResponsiveContainer>
                </div>
              </div>

              <div className="bg-white rounded-2xl border border-slate-200 p-4 shadow-sm">
                <h3 className="text-lg font-bold text-slate-800 mb-3">By Role</h3>
                <div style={{ height: 260 }}>
                  <ResponsiveContainer width="100%" height="100%">
                    <PieChart>
                      <Pie data={roleBreakdown} dataKey="value" nameKey="name" outerRadius={80} fill="#8884d8" label>
                        {roleBreakdown.map((entry, index) => (
                          <Cell key={`cell-${index}`} fill={pieColors[index % pieColors.length]} />
                        ))}
                      </Pie>
                      <Tooltip />
                      <Legend verticalAlign="bottom" height={36} />
                    </PieChart>
                  </ResponsiveContainer>
                </div>
              </div>
            </div>

            <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
              <div className="bg-white rounded-2xl border border-slate-200 p-4 shadow-sm lg:col-span-2">
                <h3 className="text-lg font-bold text-slate-800 mb-3">Messages by Team</h3>
                <div style={{ height: 300 }}>
                  <ResponsiveContainer width="100%" height="100%">
                    <BarChart data={teamBreakdown} margin={{ top: 5, right: 20, left: 0, bottom: 5 }}>
                      <CartesianGrid strokeDasharray="3 3" />
                      <XAxis dataKey="team" />
                      <YAxis />
                      <Tooltip />
                      <Legend />
                      <Bar dataKey="messages">
                        {teamBreakdown.map((entry, idx) => (
                          <Cell key={`bar-${idx}`} fill={barColors[idx % barColors.length]} />
                        ))}
                      </Bar>
                    </BarChart>
                  </ResponsiveContainer>
                </div>
              </div>

              <div className="space-y-4">
                <div className="bg-white rounded-2xl border border-slate-200 p-4 shadow-sm">
                  <h4 className="text-md font-semibold text-slate-800 mb-2">Top Responders</h4>
                  <div className="divide-y">
                    {topResponders.map((t, idx) => (
                      <div key={t.name} className="flex items-center justify-between py-2">
                        <div className="flex items-center gap-3">
                          <div className="w-9 h-9 rounded-md bg-slate-100 flex items-center justify-center font-semibold text-slate-700">{t.name.split(" ")[0][0]}</div>
                          <div>
                            <div className="text-sm font-medium text-slate-800">{t.name}</div>
                            <div className="text-xs text-slate-500">{t.role}</div>
                          </div>
                        </div>
                        <div className="text-sm font-semibold text-slate-800">{t.messages}</div>
                      </div>
                    ))}
                  </div>
                </div>

                <div className="bg-white rounded-2xl border border-slate-200 p-4 shadow-sm">
                  <h4 className="text-md font-semibold text-slate-800 mb-2">Top Performers</h4>
                  <div className="divide-y">
                    {topPerformers.map((p) => (
                      <div key={p.name} className="flex items-center justify-between py-2">
                        <div className="flex items-center gap-3">
                          <div className="w-9 h-9 rounded-md bg-slate-100 flex items-center justify-center font-semibold text-slate-700">{p.name.split(" ")[0][0]}</div>
                          <div>
                            <div className="text-sm font-medium text-slate-800">{p.name}</div>
                            <div className="text-xs text-slate-500">Performance</div>
                          </div>
                        </div>
                        <div className="text-sm font-semibold text-emerald-600">{p.metric}</div>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            </div>

            <div className="mt-6 text-sm text-slate-500">Tip: Connect this dashboard to your backend real-time stream (WebSocket / SSE) and replace the simulated interval with live updates. Keep the charts responsive by using ResponsiveContainer from recharts.</div>
          </div>
        )}
      </div>
    </div>
  );
}
